<template>
  <header>
    <NavigationBar />
  </header>
  <main>
    <div class="form-notif" v-if="form_submitted">Form submitted!</div>
    <form @submit.prevent="inquire">
      <fieldset>
        <legend>Kontak Kami</legend>
        <label for="name">Nama:</label>
        <input type="text" name="name" id="name" />
        <label for="email">E-Mail:</label>
        <input type="email" name="email" id="email" />
        <label for="inquiry">Pertanyaan:</label>
        <textarea name="inquiry" id="inquiry" cols="30" rows="10"></textarea>
        <button>Submit</button>
      </fieldset>
    </form>
  </main>
  <footer><Footer /></footer>
</template>

<script>
import axios from "axios";

import NavigationBar from "../../components/NavigationBar.vue";
import Footer from "../../components/Footer.vue";

export default {
  name: "App",
  components: {
    NavigationBar,
    Footer,
  },
  data() {
    return {
      form_submitted: false,
      apiURL: "https://tabletopid-cms.herokuapp.com",
    };
  },
  methods: {
    inquire(event) {
      let elements = event.target.elements;
      const request = {
        name: elements.name.value,
        email: elements.email.value,
        inquiry: elements.inquiry.value,
      };
      axios.post(`${this.apiURL}/inquiries`, request);
      elements.name.value = "";
      elements.email.value = "";
      elements.inquiry.value = "";
      this.form_submitted = true;
      setTimeout(() => (this.form_submitted = false), 3000);
    },
  },
};
</script>

